import React, { useState } from 'react';
import PropTypes from 'prop-types';

import Caret from '../../assets/icons/caret-solid.svg';

const Dropdown = (props) => {
  const { placeholder, options } = props;

  const [isActive, setIsActive] = useState(false);
  const [currentRegion, setCurrentRegion] = useState(placeholder);

  return (
    <div className="Dropdown">
      <div
        className={`Dropdown__Container ${
          isActive ? 'Dropdown__Container--is-Active' : ''
        }`}
        tabIndex={0}
        onClick={() => setIsActive(!isActive)}
        role="button"
      >
        <input
          type="text"
          className="Dropdown__Selection"
          id="selected"
          name="selected"
          placeholder={currentRegion}
        />
        <img src={Caret} alt="Caret" className="Dropdown__Caret" />
        <div className="Dropdown__Options-container">
          {options.length > 0 &&
            options.map((option) => (
              <div
                className="Dropdown__Option"
                key={`k__${option}`}
                onClick={(e) => setCurrentRegion(e.target.innerHTML)}
              >
                {option}
              </div>
            ))}
        </div>
      </div>
    </div>
  );
};

Dropdown.propTypes = {
  placeholder: PropTypes.string,
  options: PropTypes.arrayOf(PropTypes.string),
};

Dropdown.defaultProps = {
  placeholder: 'Filter by Region',
  options: ['Africa', 'America', 'Asia', 'Europe', 'Oceania'],
};

export default Dropdown;
